<template>
  <div id="meeting-app" v-show='meetingAppVisible'>
    <div class='page page1'>
      <div class="splash"></div>
    </div>
    <div class='page page2' v-bind:class='{ nologin:!meetingAppLogedIn }'>
      <div class="btngroup" v-if='skipInterstitial'>
        <div class="uibutton s24 green btn_launch1" v-on:click='launch(true)'>Join</div>
        <div class="uibutton s24 green btn_launch2" v-on:click='launch(false)'></div>
      </div>
      <div class="btngroup" v-if='!skipInterstitial'>
        <div class="uibutton s24 green btn_launch1b " v-on:click='launch(false)'>
          Join
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      seconds_last: 900,
      timer_interval_id: 0,
    };
  },
  computed: {
    meetingAppVisible() {
      return this.$store.state.meetingAppVisible;
    },
    meetingAppLogedIn() {
      return this.$store.state.meetingAppLogedIn;
    },
    meetingAppShowSplash() {
      return this.$store.state.meetingAppShowSplash;
    },
    skipInterstitial() {
      return this.$store.state.skipInterstitial;
    },
    audioType() {
      return this.$store.state.audioType;
    },
    interstitialCamOn() {
      return this.$store.state.interstitialCamOn;
    },
  },
  mounted() {
    this.startInCallTimer();
  },
  watch: {
    meetingAppVisible(newVal) {
      if(newVal){
        if(this.meetingAppShowSplash){
          this.showPage(1);
          $('.splash').velocity({ 
            top: 200 
            }, { 
              duration: 1000,
              queue: false,
            });
          setTimeout(() => {
            this.showPage(2);
          }, 1000);
        }else{
          this.showPage(2);
        }
      }
    },
  },
  methods: {
    showPage(id) {
      $('#meeting-app .page').hide();
      $('#meeting-app .page' + id).show();
    },
    launch(skip) {
      if(skip){
        this.$store.commit('mainclientWithoutAnimation', true);
        this.$store.commit('skipInterstitial', true);
        $('#interstitial').hide();

        this.$store.commit('isWarmUp', true);
        this.$store.commit('isInterstitial', false);

        if (this.audioType == 'pc' || this.audioType == 'callme') {
          this.$store.commit('audioConnected', true);
        }
        
        if (this.interstitialCamOn) {
          this.$store.commit('videoConnected', true);
          this.$store.commit('cameraOn', true);
        }

      }else{
        this.$store.commit('mainclientWithoutAnimation', false);
        this.$store.commit('interstitialFirstTime', false);
        this.$store.commit('isInterstitial', true);
        $('#interstitial').show();
      }
      $('#mainclient').show();

      $('#meeting_alert_in_desktop').hide();
    },

    // -----------------------------------
    //          in call timer
    // -----------------------------------
    setInCallTimer(){
    
      function formatNum(n){
        if(n < 10){
          return ('0'+n);
        }else{
          return n;
        }
      }
      if(this.seconds_last > 0){
        this.seconds_last--;
      }
      var m = Math.floor(this.seconds_last/60);
      var s = this.seconds_last%60
      
      //$('.btn_launch1b.timer').text(formatNum(m) + ':' + formatNum(s));

    },
    startInCallTimer(){
      if(this.timer_interval_id == 0){
        this.timer_interval_id = setInterval(() => this.setInCallTimer(), 1000);
        this.setInCallTimer();
      }
    },
    stopInCallTimer(roomid){
      clearInterval(this.timer_interval_id);
      this.timer_interval_id = 0;
    },

  },
};
</script>

