<template>
  <transition name="fade">
  <div id='pop_ac_setting2' 
    v-show='popAVSettingVisible2' 
    v-bind:style='{height: audioType === "pc" ? "560px" : "320px", top: audioType === "pc" ? "118px" : "200px"}'
    >
        <div class='title'>
            {{ audioType === 'pc' ? 'Audio Device and Camera Settings' : 'Camera Settings'}}
            <div class='close' v-on:click.stop='close' ></div>
        </div>
        <div class='wrap'>
          <div class='section_audio' 
            v-show='audioType == "pc"'
            style="text-align: left;"
            >
            <div class='subtitle' style='padding-bottom:6px;'>Audio</div>
            <div class='row'>
              <div class='ico_speaker flex_none'></div>
              <select 
              class='select'
              id='select_speaker'
              >
                <option selected='selected'>System default speaker</option>
              </select>
              <div class='uibutton s32' style='margin-left: 10px; width: 64px;'>Test</div>
            </div>

            <div class='row indentrow' style='margin-bottom: 16px;'>
              <div class='slider'></div>
            </div>

            <div class='row'>
              <div class='ico_mic flex_none'></div>
              <select 
              class='select'
              id='select_mic'
              >
                <option selected='selected'>System default microphone</option>
              </select>
            </div>

            <div class='row indentrow' style='margin-bottom: 6px;'>
              <div class='slider'></div>
            </div>

            <div class='row indentrow'>
              <div class='uicheckbox on' >Automatically adjust volume</div>
            </div>
            
          </div>
          <div class='section_video' style="text-align: left;">
            <div class='subtitle'>Camera</div>
            <div class='row'>
              <div class='ico_cam flex_none'></div>
              <select 
              class='select' 
              id='select_cam_conn'
              >
                <option value='cam1' selected='selected'>Microsoft LifeCam Font</option>
                <option value='cam2' >Laptop Camera</option>
                <option value='nocam' >No Video</option>
              </select>
            </div>

            <div id='video_preview'>
              <div class='video'>
                <video id="camera_preview_av" autoplay="true" ></video>
                <!-- div class='lb'>Preview Only</div -->
              </div>
            </div>

          </div>

        </div>
        
    </div>
    </transition>
</template>

<script>
export default {
  data() {
    return {
      domParent: null,
      domSelf: null,
      cameraSelected: true,
    };
  },
  computed: {
    audioType() {
      return this.$store.state.audioType;
    },
    popAVSettingVisible2() {
      return this.$store.state.popAVSettingVisible2;
    },
  },
  mounted() {
    $('.select').selectmenu({
      select: (evt, ui) => {
        if (evt.target.id == 'select_cam_conn') {
          if (ui.item.value == 'nocam') {
            this.cameraSelected = false;
            $('#video_preview').hide();
          } else {
            this.cameraSelected = true;
            $('#video_preview').show();
            this.$store.commit('selectedCameraName', ui.item.label);
          }
        }
      },
    });
    this.domParent = $('#con_main');
    this.domSelf = $('#pop_ac_setting2');
    window.addEventListener('mousedown', this.onMouseDown);
    this.initCam();
  },
  watch: {
    
  },
  methods: {
    initCam() {
      var selfViewVideo = document.getElementById('camera_preview_av');

      // Prefer camera resolution nearest to 1280x720.
      var constraints = { audio: false, video: { width: 1280, height: 720 } };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(mediaStream) {
          var video = selfViewVideo;
          video.srcObject = mediaStream;
          video.onloadedmetadata = function(e) {
            video.play();
          };
        })
        .catch(function(err) {
          console.log(err.name + ': ' + err.message);
        });
    },
    close(evt) {
      this.$store.commit('popAVSettingVisible2', false);
    },
    onMouseDown(evt){
      if($('.ui-selectmenu-open').length){
        if( !$('.ui-selectmenu-open').hitTest(evt.pageX, evt.pageY) && $('#pop_ac_setting2').length && !$('#pop_ac_setting2').hitTest(evt.pageX, evt.pageY) ){
            this.$store.commit('popAVSettingVisible2', false);
        }
      }else{
        if( $('#pop_ac_setting2').length && !$('#pop_ac_setting2').hitTest(evt.pageX, evt.pageY) ){
            this.$store.commit('popAVSettingVisible2', false);
        }
      }
    },
  },
  beforeDestroy(){
      window.removeEventListener('mousedown', this.onMouseDown);
  },
};
</script>

