<template>
  <transition name="fade">
	<div id='pop-audio-option' 
        v-show='popAudioOptionVisible' 
        v-bind:style='{width: audioType=="callme" ? "374px" : "270px"}' 
        >
        <div class='wrap'>

            <div class='itm menuitm itm_pc ico_pc' v-bind:class='{selected: audioType=="pc"}' v-on:click='selectItem("pc")'>
                <span class="lb">Use computer for audio</span>
                <span class="ico_selected" v-show='audioType=="pc"'></span>
            </div>
            
            <div class='itm menuitm itm_callme ico_callme' v-bind:class='{selected: audioType=="callme"}' v-on:click='selectItem("callme")'>
                <span class="lb">Call Me</span>
                <span class="ico_selected" v-show='audioType=="callme"'></span>
            </div>
            <div class='itm itm_callme_expand' v-show='audioType=="callme"'>
                <svg width="236px" height="32px" viewBox="0 0 236 32" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                    <g id="Symbols" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <g id="input">
                            <rect id="bg" stroke-opacity="0.16" stroke="#000000" x="0.5" y="0.5" width="235" height="31" rx="4"></rect>
                            <g id="navigation/arrow-down_12" transform="translate(212.000000, 10.000000)" fill="#000000">
                                <g id="arrow-down_12" transform="translate(1.000000, 3.000000)">
                                    <path d="M2.50048718,7.99937642 C2.36348718,7.99937642 2.22748718,7.94337642 2.12848718,7.83437642 C1.94348718,7.62837642 1.96048718,7.31237642 2.16548718,7.12737642 L6.75248718,2.99937642 L2.16548718,-1.12862358 C1.96048718,-1.31362358 1.94348718,-1.62962358 2.12848718,-1.83562358 C2.31348718,-2.03962358 2.62948718,-2.05662358 2.83448718,-1.87262358 L7.83448718,2.62737642 C7.94048718,2.72337642 8.00048718,2.85737642 8.00048718,2.99937642 C8.00048718,3.14137642 7.94048718,3.27537642 7.83448718,3.37137642 L2.83448718,7.87137642 C2.73948718,7.95737642 2.61948718,7.99937642 2.50048718,7.99937642" id="fill" transform="translate(5.000262, 2.999524) scale(-1, 1) rotate(90.000000) translate(-5.000262, -2.999524) "></path>
                                </g>
                            </g>
                            <path d="M68,0.5 L68,31.2144185" id="Line" stroke-opacity="0.16" stroke="#000000" stroke-linecap="square"></path>
                        </g>
                    </g>
                </svg>
                <div class="flag">🇺🇸</div>
                <div class="code">+1</div>
                <input class="phone-input" type="text" placeholder="" value="" v-on:change="onInputChange()">
            </div>

            <div class='itm menuitm itm_callin ico_callin' v-bind:class='{selected: audioType=="callin"}' v-on:click='selectItem("callin")'>
                <span class="lb">Call In</span>
                <span class="ico_selected" v-show='audioType=="callin"'></span>
            </div>

            <div class='itm menuitm itm_none ico_none' v-bind:class='{selected: audioType=="none"}' v-on:click='selectItem("none")'>
                <span class="lb">Don't connect audio</span>
                <span class="ico_selected" v-show='audioType=="none"'></span>
            </div>

        </div>
        
    </div>
    </transition>
</template>

<script>

export default {
    data() {
        return {
        }
    },
    computed:{
        audioConnected(){
            return this.$store.state.audioConnected;
        },
        popAudioOptionVisible(){
            return this.$store.state.popAudioOptionVisible;
        },
        popStartPoint(){
            return this.$store.state.popStartPoint;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        
    },
    mounted(){
        
    },
    watch: {
        popAudioOptionVisible: function (newVal) {
            if(newVal){
                let left = this.popStartPoint[0];
                let top = this.popStartPoint[1] + 80;
                $('#pop-audio-option').css('left', left+'px');
                $('#pop-audio-option').css('top', top+'px');
                window.addEventListener('mousedown', this.onMouseDown);
            }else{
                window.removeEventListener('mousedown', this.onMouseDown);
            }
        }
    },
    methods:{
        onMouseDown(evt){
            if( !$('#interstitial-audio-button').hitTest(evt.pageX, evt.pageY) && $('#pop-audio-option').length && !$('#pop-audio-option').hitTest(evt.pageX, evt.pageY) ){
                this.$store.commit('popAudioOptionVisible', false);
            }
        },
        selectItem(type){
            this.$store.commit('audioType', type);
        },
        onInputChange(){
            this.$store.commit('callmePhoneNumber', $('.phone-input').val());
        },
    },
    beforeDestroy(){
        window.removeEventListener('mousedown', this.onMouseDown);
    },
    

}
</script>

