          <div class='thumb'></div>
          <div class='name'>{{devicePairedName}}</div>
          <div class='btn_call uibutton grey s32' v-on:click='selectDifferentDevice'>Use a Different Device</div>
          <div class='btn_call uibutton red s32' v-on:click='disconnectDevice'>Disconnect</div>
        </div>
      </div>
</template>

<script>

export default {
    data() {
        return {
            domParent: null,
            domSelf: null,
            videoAddressView: false,
        }
    },
    computed:{
        popDevicePairingVisible(){
            return this.$store.state.popDevicePairingVisible;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        devicePaired(){
            return this.$store.state.devicePaired;
        },
        devicePairedName(){
            return this.$store.state.devicePairedName;
        },
    },
    mounted(){
        this.domParent = $('#con_main');
        this.domSelf = $('#pop_device_pairing');
    },
    watch: {

    },
    methods:{
      selectDevice(name, videoaddress=false) {
        this.$store.commit('devicePaired', true);
        this.$store.commit('devicePairedName', name);
        this.$store.commit('audioType', 'none');
        this.close();
      },
      selectDifferentDevice(evt) {
        this.$store.commit('devicePaired', false);
      },
      disconnectDevice(evt) {
        this.$store.commit('devicePaired', false);
        this.close();
      },
      searchDevice(evt) {
        this.videoAddressView = true;
      },
      closeSearchDevice(evt) {
        this.videoAddressView = false;
      },
      close(evt){
          this.$store.commit('popDevicePairingVisible', false); 
      },
    }

}
</script>

