<template>
	<div 
		id="mainclient" 
		class='con con_flex_column' 
		v-bind:class='{
			trans: !mainclientWithoutAnimation, 
			interstitial: isInterstitial, 
			normal: !fullscreen && !isInterstitial, 
			fullscreen: fullscreen && !isInterstitial
		}'
		>
		<con-top v-show='!isInterstitial'></con-top>
		<con-main></con-main>
		<interstitial></interstitial>
		<div id='popover'>
			<pop-audio></pop-audio>
			<pop-video></pop-video>
			<pop-device-pairing></pop-device-pairing>
			<pop-av-setting2></pop-av-setting2>
			<!--pop-avconnection></pop-avconnection-->
			<pop-share></pop-share>
			<pop-info></pop-info>
			<!--popmenu-panel></popmenu-panel-->
			<poptip></poptip>
			<pop-record></pop-record>
			<pop-lockpeople></pop-lockpeople>
			<pop-callin></pop-callin>
			<popmenu-more></popmenu-more>
			<popmenu-switchsharing></popmenu-switchsharing>
			<!--tip-coachmark
				v-bind:visible='coachmark_visible'
				v-bind:buttonLabel='coachmark_buttonLabel'
				v-bind:message='coachmark_message'
				v-bind:callback='coachmark_callback'
				v-bind:top='coachmark_top'
				v-bind:left='coachmark_left'
				v-bind:width='coachmark_width'
				v-bind:arrowDirection='coachmark_arrowDirection'
			></tip-coachmark-->
		</div>
		<tip-message v-bind:label='notification' v-bind:show='showNotification' v-bind:style='{width:"calc(100% - " + (panelVisible ? rightPanelContentWidth : 0) + "px)"}'></tip-message>
		<!--audio id="people_talking" loop="loop" preload="auto" src="../sound/people_talking.m4a"></audio-->
	</div>
</template>

<script>

export default {
    data() {
        return {
					people_talking: null,
					notification: null,
					showNotification: false,

					coachmark_visible: false,
					coachmark_buttonLabel: 'Next',
					coachmark_message: '',
					coachmark_callback: null,
					coachmark_top: 200,
					coachmark_left: 200,
					coachmark_width: '344px',
					coachmark_arrowDirection: 'up',
        }
    },
    computed:{
			activeSpeakerName(){
				return this.$store.state.activeSpeakerName;
			},
			activeSpeakerList(){
				return this.$store.state.activeSpeakerList;
			},
			isInterstitial(){
				return this.$store.state.isInterstitial;
			},
			fullscreen(){
				return this.$store.state.fullscreen;
			},
			mainclientWithoutAnimation(){
				return this.$store.state.mainclientWithoutAnimation;
			},
			audioConnected(){
				return this.$store.state.audioConnected;
			},
			isInterstitial(){
				return this.$store.state.isInterstitial;
			},
			isWarmUp(){
				return this.$store.state.isWarmUp;
			},
			showCallmeCalling(){
				return this.$store.state.showCallmeCalling;
			},
			muted(){
				return this.$store.state.muted;
			},
			interstitialFirstTime(){
				return this.$store.state.interstitialFirstTime;
			},
			isFTE(){
				return this.$store.state.isFTE;
			},
			FTE_step(){
				return this.$store.state.FTE_step;
			},
			FTE_completed(){
				return this.$store.state.FTE_completed;
			},
			panelVisible(){
        return this.$store.state.panelVisible;
      },
			rightPanelContentWidth() {
      	return this.$store.state.rightPanelContentWidth;
    	},
		},
		mounted() {
			this.people_talking = document.getElementById('people_talking');

			this.autoSwitchActiveSpeaker();
		},
		watch: {
      audioConnected(newVal) {
      	setTimeout(() => this.checkAudioPlay(), 500);
			},
			isInterstitial(newVal) {
				setTimeout(() => this.checkAudioPlay(), 500);
			},
			isWarmUp(newVal) {
      	setTimeout(() => this.checkAudioPlay(), 500);
    	},
			showCallmeCalling(newVal) {
      	setTimeout(() => this.checkAudioPlay(), 500);
			},
			muted(newVal) {
				if(newVal){
					if(!this.isInterstitial){
						this.notification = 'Your microphone is muted';
						this.showNotification = true;
						setTimeout(() => {
							this.showNotification = false;
						}, 3000);
					}
				}
			},

			interstitialFirstTime(newVal) {
        if(!newVal){
					this.$store.commit('FTE_completed', false);
					this.coachmark_visible = true;
					this.setCoachmark(1);
        }
			},

			isFTE(newVal) {
        this.coachmark_visible = newVal;
			},
		},
		methods: {
			autoSwitchActiveSpeaker() {
      setInterval(this.switchActiveSpeaker, 7000);
    },

    switchActiveSpeaker() {
      let idx = this.activeSpeakerList.indexOf(this.activeSpeakerName);
      if (idx < this.activeSpeakerList.length - 1) {
        idx++;
      } else {
        idx = 0;
      }
      this.$store.commit("activeSpeakerName", this.activeSpeakerList[idx]);
	},
	


			checkAudioPlay(){
				if(this.audioConnected && !this.isInterstitial && !this.isWarmUp && !this.showCallmeCalling){
					this.people_talking.pause();
					this.people_talking.play();
				}else{
					this.people_talking.pause();
				}
			},
			setCoachmark(step){
				if(step === 1){
					this.coachmark_message = 'Choose how you want to connect to audio';
					this.coachmark_callback = () => this.setCoachmark(2);
					this.coachmark_top = '670px';
					this.coachmark_left = '346px';
					this.coachmark_width = '344px';
					this.coachmark_arrowDirection = 'up';
				}else if(step === 2){
					this.$store.commit('FTE_step', 'audio-video-button');

					this.coachmark_message = 'Mute your microphone or turn off your video when you join the meeting. You can always change it later.';
					this.coachmark_callback = () => this.setCoachmark(3);
					this.coachmark_top = '421px';
					this.coachmark_left = '266px';
					this.coachmark_width = '344px';
					this.coachmark_arrowDirection = 'down';
				}else if(step === 3){
					this.$store.commit('FTE_step', 3);

					this.coachmark_message = 'Select a different camera or headset';
					this.coachmark_callback = () => this.setCoachmark(4);
					this.coachmark_top = '666px';
					this.coachmark_left = '619px';
					this.coachmark_width = '311px';
					this.coachmark_arrowDirection = 'up';
				}else if(step === 4){
					this.coachmark_message = 'Connect to a Cisco video device or a Cisco Webex Share device';
					this.coachmark_buttonLabel = 'OK',
					this.coachmark_callback = () => {
						this.coachmark_visible = false;
						this.$store.commit('isFTE', false);
						this.$store.commit('FTE_completed', true);
						};
					this.coachmark_top = '687px';
					this.coachmark_left = '38px';
					this.coachmark_width = '274px';
					this.coachmark_arrowDirection = 'up';
				}
			},
		},
}
</script>

<style scoped>

#mainclient{
	position: absolute;
	background: #FFF;
}
.trans {
	transition: all 0.3s ease-out;
}
.interstitial {
	width: 704px;
	height: 578px;
	left: calc((100% - 704px)/2);
	top: calc((100% - 578px - 36px)/2);
	box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.5), 0px 4px 12px rgba(0, 0, 0, 0.15);
}
.normal {
	width: 1366px;
	height: 750px;
	top: 0;
	left: 0;
	border: 0px solid rgba(41,41,41,0.32);
}
.fullscreen{
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	border: none;
}

</style>