<template>
  <div id='self_video' 
  v-show='cameraOn && videoConnected || (isInterstitial || isWarmUp) && interstitialCamOn' 
  v-on:mousemove='mousemove' 
  v-bind:class='{
    min:minimized,
    interstitial:isInterstitial,
    warmup:isWarmUp,
    }' 
  >
        <video id="camera" autoplay="true"></video>
        <!--div class='lb_selected'>{{selectedCameraName}}</div-->
        <div 
        class='btn_minimize' 
        v-if='!isInterstitial && !isWarmUp'
        v-tooltip.hover.click.bottom='minimized ? "Restore this Window" : "Minimize Selfvideo (Other will continue to see my video)"' v-on:click.stop='toggleMinimize' v-bind:class='{min:minimized}'></div>
    </div>
</template>

<script>
const marginX = 15;
const marginTop = 60;
const marginBottom = 14;

export default {
  data() {
    return {
      domVideo: null,
      domParent: null,
      domCon: null,
      minimized: false,
    };
  },
  computed: {
    isInterstitial() {
      return this.$store.state.isInterstitial;
    },
    isWarmUp() {
      return this.$store.state.isWarmUp;
    },
    interstitialCamOn() {
      return this.$store.state.interstitialCamOn;
    },
    panelVisible() {
      return this.$store.state.panelVisible;
    },
    rightPanelWidth() {
      return this.$store.state.rightPanelWidth;
    },
    chatPanelVisible() {
      return this.$store.state.chatPanelVisible;
    },
    cameraOn() {
      return this.$store.state.cameraOn;
    },
    fullscreen() {
      return this.$store.state.fullscreen;
    },
    headerHeight() {
      return this.$store.state.headerHeight;
    },
    videoLayoutMode() {
      return this.$store.state.videoLayoutMode;
    },
    activeSpeaker() {
      return this.$store.getters.activeSpeaker;
    },
    currentSharing() {
      return this.$store.state.currentSharing;
    },
    floatVideoSize() {
      return this.$store.state.floatVideoSize;
    },
    videoConnected() {
      return this.$store.state.videoConnected;
    },
    selectedCameraName() {
      return this.$store.state.selectedCameraName;
    },
    selfVideoPosition() {
      return this.$store.state.selfVideoPosition;
    },
  },
  mounted() {
    this.domVideo = $('#self_video');
    this.domParent = $('#con_main');
    this.domCon = $('#con_left');
    this.initCam();
    this.initDrag();
    window.addEventListener('resize', this.onResize);
  },
  watch: {
    panelVisible(newVal) {
      this.onResize();
    },
    rightPanelWidth(newVal) {
      this.onResize();
    },
    videoLayoutMode(newVal) {
      if (newVal != 'side-by-side') {
        this.onResize();
      }
    },
    floatVideoSize(newVal) {
      this.onResize();
    },
    videoConnected(newVal) {
      this.moveToCorner(false, 'BR');
    },
    fullscreen(newVal) {
      let cnt = 0;
      let itv = setInterval(() => {
        cnt++;
        this.onResize();
        if (cnt >= 100) {
          clearInterval(itv);
        }
      }, 10);
    },
    selfVideoPosition(newVal) {
      if(newVal == 'BR'){
        this.moveToCorner(true, newVal);
      }
    },
  },
  methods: {
    initCam() {
      var selfViewVideo = document.getElementById('camera');

      // Prefer camera resolution nearest to 1280x720.
      var constraints = { audio: false, video: { width: 1280, height: 720 } };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(mediaStream) {
          var video = selfViewVideo;
          video.srcObject = mediaStream;
          video.onloadedmetadata = function(e) {
            video.play();
          };
        })
        .catch(function(err) {
          console.log(err.name + ': ' + err.message);
        });
    },
    initDrag() {
      // self video draggable
      let ww = !this.minimized ? this.domVideo.width() : 20;
      let wh = !this.minimized ? this.domVideo.height() : 20;

      let offset = this.domParent.offset();
      let panew = this.domParent.width(); //this.chatPanelVisible ? (this.domParent.width()-this.rightPanelWidth) : this.domParent.width();
      let x1 = offset.left + marginX;
      let x2 = offset.left + panew - ww - marginX;
      let y1 = offset.top + marginTop;
      let y2 = offset.top + this.domCon.height() - wh - marginBottom;
      $('#self_video').draggable({
        stack: '.floating_window',
        containment: [x1, y1, x2, y2],
        start: () => {},
        drag: () => {},
        stop: () => {
          this.moveToCorner(true);
        },
      });
    },
    onResize() {
      this.initDrag();
      this.moveToCorner();
    },
    moveToCorner(animated = false, corner = null, size = null) {
      if ($('#self_video').hasClass('fixedPosition')) {
        return;
      }
      let ww = !this.minimized ? 160 : 20;
      let wh = !this.minimized ? 90 : 20;



      let ox = this.domVideo.position().left + ww / 2;
      let oy = this.domVideo.position().top + wh / 2;
      let panew = 1366;//this.domParent.width(); //this.chatPanelVisible ? (this.domParent.width()-this.rightPanelWidth) : this.domParent.width();
      let pox = panew / 2;
      let poy = this.domCon.height() / 2;
      let top = 'auto', left = 'auto', right = 'auto', bottom = 'auto';
      if (corner == 'TL' || (corner === null && ox <= pox && oy <= poy)) {
        // TL
        top = marginTop + 'px';
        left = marginX + 'px';

        this.domVideo.css('right', right);
        this.domVideo.css('bottom', bottom);

        if (animated) {
          this.domVideo.velocity({ 
            left: left, 
            top: top,
            }, { 
              duration: 300, 
              queue: false,
            });
        }
      } else if (corner == 'TR' || (corner === null && ox > pox && oy <= poy)) {
        // TR
        top = marginTop + 'px';
        right = marginX + 'px';

        this.domVideo.css('bottom', bottom);
        this.domVideo.css('left', left );

        if (animated) {
          this.domVideo.velocity({ 
            right: right, 
            top: top,
            }, { 
              duration: 300, 
              queue: false,
            });
        }
      } else if (corner == 'BR' || (corner === null && ox > pox && oy > poy)) {
        // BR
        bottom = marginBottom + 'px';
        right = marginX + 'px';

        this.domVideo.css('right', right);
        this.domVideo.css('bottom', bottom);

        if (animated) {
          this.domVideo.velocity({ 
            right: right, 
            bottom: bottom 
            }, { 
              duration: 300, 
              queue: false,
            });
        }
      } else if (corner == 'BL' || (corner === null && ox <= pox && oy > poy)) {
        // BL
        bottom = marginBottom + 'px';
        left = marginX + 'px';

        this.domVideo.css('bottom', bottom);
        this.domVideo.css('left', left );

        if (animated) {
          this.domVideo.velocity({ 
            left: left, 
            bottom: bottom 
            }, { 
              duration: 300, 
              queue: false,
            });
        }
      }

      if (!animated) {
        this.domVideo.css('right', right);
        this.domVideo.css('bottom', bottom);
        this.domVideo.css('left', left );
        this.domVideo.css('top', top);
      }
    },
    mousemove(evt) {
      $('.btn_minimize').show();
      window.addEventListener('mousemove', this.globalMousemove);
    },
    globalMousemove(evt) {
      if (!$('#self_video').hitTest(evt.pageX, evt.pageY)) {
        $('.btn_minimize').hide();
        window.removeEventListener('mousemove', this.globalMousemove);
      }
    },
    toggleMinimize(evt) {
      if (this.minimized) {
        this.minimized = false;
        setTimeout(() => {
          this.initDrag();
          this.moveToCorner(true, 'BR');
        }, 10);
      } else {
        this.minimized = true;
        this.initDrag();
        this.moveToCorner(true, 'BR');
      }
    },
  },
};
</script>

<style>
.fixedPosition {
  pointer-events: none;
  border-radius: 3px !important;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.3) !important;
}
</style>

<style scoped>
#self_video {
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
  transition-property: "width height";
  position: absolute;
  width: 160px;
  height: 90px;
  border-radius: 6px;
  overflow: hidden;
  transform: rotate(0deg);
  background-color: #fff;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.3), 0px 0px 8px rgba(0, 0, 0, 0.1);
}
#self_video.min {
  width: 20px;
  height: 20px;
  border-radius: 4px;
}
#self_video.min video {
  display: none;
}
#self_video.min .lb_selected {
  display: none;
}
#self_video.interstitial {
  position: absolute;
  top: 0 !important;
  left: 50% !important;
  width: 100%;
  height: 100%;
  transform: translate(-50%, 0%);
  border-radius: 0;
}
#self_video.warmup {
  position: absolute;
  top: 50% !important;
  left: 50% !important;
  width: 100%;
  height: 115%;
  transform: translate(-50%, -50%) scale(-1, 1);
  border-radius: 0;
}


#camera {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(-1, 1);
  width: auto;
  height: 100%;
}

.btn_minimize {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  pointer-events: auto;
  cursor: pointer;
  background: url(../img/icon/ico_min_self.svg) no-repeat center;
  display: none;
  background-color: #fff;
}
.btn_minimize.min {
  top: 0;
  left: 0;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  border-radius: 0px;
  background: url(../img/icon/ico_restore_self.svg) no-repeat center;
  display: block !important;
}
.lb_selected {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  text-align: center;
  color: #FFF;
  background-color: rgba(0, 0, 0, 0.5);
}
</style>