      <div class="btn_next" v-on:click='showCoachmark()'></div>
    </div>
    
    <!--div class='fte_disabled_mask' v-if='isFTE'></div-->

    <tip-message v-bind:label='notification' v-bind:show='showNotification' 
    v-bind:style='{bottom:"160px"}'
    ></tip-message>

	</div>
</template>

<script>

export default {
    data() {
        return {
          name:'Cisco Webex Meetings',
          audio_option_label:'Use Computer for audio',
          callmenumber:'Call me at ',
          paired_name:'',
          isVideoAddress:false,
          deviceMenuVisible:false,
          marquee_intervalId:null,
          marquee_currentId:0,
          marquee_paused:false,
          settingIconVisible:false,
          notification: null,
					showNotification: false,
        }
    },
    computed:{
        inMeeting(){
        	return this.$store.state.inMeeting;
        },
        callmePhoneNumber(){
        	return this.$store.state.callmePhoneNumber;
        },
        meetingTitle(){
            return this.$store.state.meetingTitle;
        },
        popAudioOptionVisible(){
            return this.$store.state.popAudioOptionVisible;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        interstitialFirstTime(){
            return this.$store.state.interstitialFirstTime;
        },
        interstitialSkipLogin(){
            return this.$store.state.interstitialSkipLogin;
        },
        isFTE(){
            return this.$store.state.isFTE;
        },
        FTE_step(){
            return this.$store.state.FTE_step;
        },
        FTE_completed(){
            return this.$store.state.FTE_completed;
        },
        skipInterstitial(){
            return this.$store.state.skipInterstitial;
        },
        interstitialCamOn(){
            return this.$store.state.interstitialCamOn;
        },
        devicePaired(){
            return this.$store.state.devicePaired;
        },
        isInterstitial(){
            return this.$store.state.isInterstitial;
        },
        headerDefaultHeight(){
            return this.$store.state.headerDefaultHeight;
        },
        muted(){
            return this.$store.state.muted;
        },
    },
    mounted() {
      this.marquee();
      window.addEventListener('mousedown', this.onMouseDown);
    },
    watch: {
      audioType(newVal) {
        if(newVal == 'pc'){
            this.audio_option_label = 'Use Computer for audio';
            this.marquee_paused = false;
        }else if(newVal == 'callme'){
            this.audio_option_label = '';
            this.marquee_paused = true;
        }else if(newVal == 'callin'){
            this.audio_option_label = 'Call in';
            this.marquee_paused = true;
        }else if(newVal == 'none'){
            this.audio_option_label = "Don't connect audio";
            this.marquee_paused = true;
        }
      },
      isInterstitial(newVal) {
        if(newVal){
          this.$store.commit('headerHeight', 0);
        }else{
          this.$store.commit('headerHeight', this.headerDefaultHeight);
        }

        if(newVal && !this.FTE_completed){
          this.$store.commit('isFTE', true);
        }
      },
      interstitialFirstTime(newVal) {
        if(newVal){
          setTimeout(() => {
            this.initCam()
          }, 10);
        }
      },
    },
    methods: {
      initCam() {
        var selfViewVideo = document.getElementById('camera_login_form');
        console.log(selfViewVideo);
        // Prefer camera resolution nearest to 1280x720.
        var constraints = { audio: false, video: { width: 1280, height: 720 } };
        navigator.mediaDevices
          .getUserMedia(constraints)
          .then(function(mediaStream) {
            var video = selfViewVideo;
            video.srcObject = mediaStream;
            console.log(video);
            video.onloadedmetadata = function(e) {
              video.play();
            };
          })
          .catch(function(err) {
            console.log(err.name + ': ' + err.message);
          });
      },
      joinMeeting(evt) {
        //this.$store.commit('isWarmUp', true);
        this.$store.commit('isInterstitial', false);

        if (this.audioType == 'pc' || this.audioType == 'callme') {
          this.$store.commit('audioConnected', true);
        }
        
        if (this.interstitialCamOn) {
          this.$store.commit('videoConnected', true);
          this.$store.commit('cameraOn', true);
        }

        $('#interstitial').fadeOut(100);
        this.$store.commit('isFTE', false);

        // default open plist
        setTimeout(() => {
          this.$store.commit('plistPanelVisible', true);
          this.$store.commit('panelVisible', true);
        }, 300);

        setTimeout(() => {
          this.$store.commit('showMdpPage', true);
        }, 2000);
      },
      showPopAudioOption(evt) {
        if (!this.popAudioOptionVisible) {
          let appos = $('#mainclient').offset();
          let offset = $('.audio-options-row .audio-button').offset();
          let left = offset.left - appos.left + $('.audio-options-row .audio-button').width() / 2;
          let top = offset.top - appos.top;
          this.$store.commit('popStartPoint', [left, top]);
          this.$store.commit('popAudioOptionVisible', true);
        } else {
          this.$store.commit('popAudioOptionVisible', false);
        }
      },
      showSettings(evt) {
        this.$store.commit('popAVSettingVisible', true);
      },
      toggleVideo(evt) {
        this.$store.commit('interstitialCamOn', !this.interstitialCamOn);

        if(this.interstitialCamOn){
          this.notification = 'Camera is on';
        }else{
          this.notification = 'Camera is off';
        }
        this.showNotification = true;
        clearTimeout(this.itv_notification);
        this.itv_notification = setTimeout(() => {
          this.showNotification = false;
        }, 3000);
      },
      toggleMute(evt) {
        this.$store.commit('muted', !this.muted);

        if(!this.muted){
          this.notification = 'Microphone is on';
        }else{
          this.notification = 'Microphone is off';
        }
        this.showNotification = true;
        clearTimeout(this.itv_notification);
        this.itv_notification = setTimeout(() => {
          this.showNotification = false;
        }, 3000);
      },
      toggleSkip(evt) {
        this.$store.commit('skipInterstitial', !this.skipInterstitial);
        
      },
      showDeviceMenu(evt) {
        this.deviceMenuVisible = true;
      },
      close(evt) {
        this.deviceMenuVisible = false;
      },
      selectDevice(name, videoaddress=false) {
        this.isVideoAddress = videoaddress;
        this.paired_name = name;
        this.$store.commit('devicePaired', true);
        this.closeSearchDevice();
        this.close();
      },
      disconnectDevice(evt) {
        this.$store.commit('devicePaired', false);
        this.deviceMenuVisible = false;
      },
      marquee() {
        const itmcnt = 3;
        const duration = 500;
        const itmh = $('.selected_devices1 .itm').height();
        let startMq = () => {

          if(this.marquee_paused || this.marquee_currentId == 3){
            clearInterval(this.marquee_intervalId);
            this.marquee_intervalId = null;
            this.settingIconVisible = true;
            return;
          }

          $('.selected_devices1 .itm').hide();
          let toshowid = this.marquee_currentId;
          let nowid;
          if(toshowid > 0){
            nowid = toshowid-1;
          }else{
            nowid = itmcnt-1;
          }
          $('.selected_devices1 .itm'+nowid).css('top', 0);
          $('.selected_devices1 .itm'+toshowid).css('top', itmh+'px');
          $('.selected_devices1 .itm'+nowid).show();
          $('.selected_devices1 .itm'+toshowid).show();

          $('.selected_devices1 .itm'+nowid).velocity(
            { top: -itmh },
            {
              duration: duration,
              queue: false,
            }
          );

          $('.selected_devices1 .itm'+toshowid).velocity(
            { top: 0 },
            {
              duration: duration,
              queue: false,
            }
          );

          if(toshowid < itmcnt-1){
            this.marquee_currentId++;
          }else{
            this.marquee_currentId = 0;
          }
        }

        this.marquee_intervalId = setInterval(startMq, 5000);
        startMq();
      },
      onMouseDown(evt){
        if( $('.device_menu').length && !$('.device_menu').hitTest(evt.pageX, evt.pageY) ){
          this.deviceMenuVisible = false;
        }
      },
      searchDevice(evt){
        $('.page_main').hide();
        $('.page_search').show();
        $('.device_menu .head .backbtn').show();
      },
      closeSearchDevice(evt){
        $('.page_main').show();
        $('.page_search').hide();
        $('.device_menu .head .backbtn').hide();
      },
      showIconVisible(show){
        if(!this.devicePaired){
          this.settingIconVisible = show || this.marquee_intervalId == null;
        }
      },
      showCoachmark(){
        if(this.interstitialFirstTime){
          this.$store.commit('isFTE', true);
          this.$store.commit('interstitialFirstTime', false);
          
        }
        this.$store.commit('initSelfCam', true);

        this.notification = 'Microphone is ' + (this.muted ? 'off' : 'on') + ' and video is ' + (this.interstitialCamOn ? 'on' : 'off');
        this.showNotification = true;
        clearTimeout(this.itv_notification);
        this.itv_notification = setTimeout(() => {
          this.showNotification = false;
        }, 3000);
      },
      
    },
    beforeDestroy() {
      clearInterval(this.marquee_intervalId);
      window.removeEventListener('mousedown', this.onMouseDown);
    },

    
}
</script>

