<template>
    <transition name="fade">
	<div id='popmenu_more' v-show='popmenuMoreVisible' >
        <div class='triangle'></div>
        <div class='wrap'>

            <div class='itm iconblock itm_notes' >
                <div class='icobtn' >
                    <span></span>
                    <svg-ico-notes></svg-ico-notes>
                    <span></span>
                </div>
                <span class="lb">Notes</span>
            </div>

            <div class='itm iconblock itm_polling '>
                <div class='icobtn'>
                    <span></span>
                    <svg-ico-polling></svg-ico-polling>
                    <span></span>
                </div>
                <span class="lb">Polling</span>
            </div>

            <div class='itm iconblock itm_viewer'>
                <div class='icobtn'>
                    <span></span>
                    <svg-ico-multimedia></svg-ico-multimedia>
                    <span></span>
                </div>
                <span class="lb">Multimedia Viewer</span>
            </div>

            <div class='itm menuitm spline' v-on:click.stop='toggleLock' >
                <span class="ico_1 ico_menu_lock"></span>
                <span class="lb" v-show='!locked' >Lock Meeting</span>
                <span class="lb" v-show='locked' >Unlock Meeting</span>
                <span class="ico ico_lock" v-show='locked'></span>
            </div>

            <div class='itm menuitm' >
                <span class="ico_1 ico_menu_steaming"></span>
                <span class="lb">Start Streaming to Facebook</span>
            </div>

            <div class='itm menuitm' >
                <span class="ico_1 ico_menu_video_sys"></span>
                <span class="lb">Show Video on Video System</span>
            </div>

            <div class='itm menuitm' >
                <span class="ico_1 ico_menu_invite"></span>
                <span class="lb">Invite and Remind</span>
            </div>

            <div class='itm menuitm' >
                <span class="ico_1 ico_menu_copy"></span>
                <span class="lb">Copy Meeting URL</span>
            </div>

            <div class='itm menuitm spline' v-on:click.stop='showAudioConnection' >
                <span class="ico_1 ico_menu_phone"></span>
                <span class="lb">Audio connection</span>
            </div>

            <div class='itm menuitm' v-if='!devicePaired && audioConnected' v-on:click.stop='showAVsetting' >
                <span class="ico_1 ico_menu_gear"></span>
                <span class="lb">{{ audioType === 'pc' ? 'Speaker, microphone, and camera' : 'Camera'}}</span>
            </div>

            <div class='itm menuitm' v-if='devicePaired' v-on:click.stop='showConnectedDevice' >
                <span class="ico_1 ico_menu_device"></span>
                <span class="lb">{{devicePairedName}}</span>
            </div>

            <!--
            <div v-if='videoConnected' class='itm menuitm' v-on:click.stop='switchCamera' >
                <span class="lb">Switch Camera</span>
            </div>
            -->

            <div class='itm menuitm spline' v-if='!devicePaired' v-on:click.stop='connectToDevice' >
                <span class="ico_1 ico_menu_device"></span>
                <span class="lb">Connect to a Device</span>
            </div>

        </div>
        
    </div>
    </transition>
</template>

<script>

export default {
    data() {
        return {
        }
    },
    computed:{
        audioConnected(){
            return this.$store.state.audioConnected;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        popmenuMoreVisible(){
            return this.$store.state.popmenuMoreVisible;
        },
        popStartPoint(){
            return this.$store.state.popStartPoint;
        },
        locked(){
            return this.$store.state.locked;
        },
        videoConnected() {
          return this.$store.state.videoConnected;
        },
        devicePaired() {
          return this.$store.state.devicePaired;
        },
        devicePairedName() {
          return this.$store.state.devicePairedName;
        },
    },
    mounted(){
        
    },
    watch: {
        popmenuMoreVisible: function (newVal) {
            if(newVal){
                let left = this.popStartPoint[0];
                let top = this.popStartPoint[1] - 20;
                $('#popmenu_more').css('left', left+'px');
                $('#popmenu_more').css('top', top+'px');
                window.addEventListener('mousedown', this.onMouseDown);
            }else{
                window.removeEventListener('mousedown', this.onMouseDown);
            }
        }
    },
    methods:{
        showAudioConnection(evt){

            let appos = $('#mainclient').offset();
            let btn = $('#controls .btn_more');
            let offset = btn.offset();
            let left = offset.left - appos.left + btn.width()/2;
            let top = offset.top- appos.top;
            this.$store.commit('popStartPoint', [left, top]);
            this.$store.commit('popAudioVisible', true);

            this.$store.commit('popmenuMoreVisible', false);
        },
        showAVsetting(evt){
            this.$store.commit('popAVSettingVisible2', true);
            this.$store.commit('popmenuMoreVisible', false);
        },
        switchCamera(evt){

            let appos = $('#mainclient').offset();
            let btn = $('#controls .btn_more');
            let offset = btn.offset();
            let left = offset.left - appos.left + btn.width()/2;
            let top = offset.top- appos.top;
            this.$store.commit('popStartPoint', [left, top]);
            this.$store.commit('popVideoIsSwitch', true);
            this.$store.commit('popVideoVisible', true);

            this.$store.commit('popmenuMoreVisible', false);
        },
        connectToDevice(evt){
            this.$store.commit('popDevicePairingVisible', true);
            this.$store.commit('popmenuMoreVisible', false);
        },
        showConnectedDevice(evt){
            this.$store.commit('popDevicePairingVisible', true);
            this.$store.commit('popmenuMoreVisible', false);
        },
        onMouseDown(evt){
            if( !$('#controls .btn_more').hitTest(evt.pageX, evt.pageY) && $('#popmenu_more').length && !$('#popmenu_more').hitTest(evt.pageX, evt.pageY) ){
                this.$store.commit('popmenuMoreVisible', false);
            }
        },
        toggleLock(evt){
            if(this.locked){
                this.$store.commit('locked', false);
                this.$store.commit('poptipText', 'The meeting room is unlocked.');
            }else{
                this.$store.commit('locked', true);
                this.$store.commit('poptipText', 'The meeting room is locked.');
            }

            /*
            let appos = $('#mainclient').offset();
            let offset = $('#controls .btn_lock').offset();
            let left = offset.left - appos.left + $('#controls .btn_lock').width()/2;
            let top = offset.top- appos.top;

            this.$store.commit('popStartPoint', [left, top]);
            this.$store.commit('poptipVisible', true);
            */

            //this.$store.commit('popmenuMoreVisible', false);
        },
    }

}
</script>

