<script>
export default {
  data() {
    return {
      seconds_last: 20,
      timer_interval_id: 0,
      skipDialogPage: false,
      checkbox_page_3b_2: false,
      jumpy_tip_visible: true,
    };
  },
  mounted() {
    if (!validateBrowser()) {
      $('.alert').show();
      $('.btn_start').addClass('disabled');
    }
    $('#mainclient').hide();
    this.$store.commit('isInterstitial', true);
    
    
  },
  computed: {
    meetingTitle(){
      return this.$store.state.meetingTitle;
    },
    fullscreen() {
      return this.$store.state.fullscreen;
    },
    interstitialSkipLogin() {
      return this.$store.state.interstitialSkipLogin;
    },
    showMdpPage() {
      return this.$store.state.showMdpPage;
    },
  },
  watch: {
    showMdpPage(newVal) {
      if(newVal){
        this.showMdp();
      }
    },
  },
  methods: {
    initCam() {
      var selfViewVideo = document.getElementById('camera_thin_client');
      console.log(selfViewVideo);
      // Prefer camera resolution nearest to 1280x720.
      var constraints = { audio: false, video: { width: 1280, height: 720 } };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(mediaStream) {
          var video = selfViewVideo;
          video.srcObject = mediaStream;
          console.log(video);
          video.onloadedmetadata = function(e) {
            video.play();
          };
        })
        .catch(function(err) {
          console.log(err.name + ': ' + err.message);
        });
    },
    showPage(id) {
      $('.slideshow .page').hide();
      $('.slideshow .page_' + id).show();

      if(id == '1_2'){
        setTimeout(() => {
          $('.txt_white_msk').hide();
        }, 6000);
      }

      if(id == '2_4'){
        setTimeout( () => {
          this.showFTE("2_5");
        }, 1000 );
      }

      if(id == '2_9'){
        setTimeout( () => {
          this.change2TestMeeting();
          // this.showPage("2_10"); // sign in
          this.showPage("2_11");
        }, 1500 );
      }

    },

    showPageFromCalendar() {
      if(this.skipDialogPage){
        this.showPage("3b_2_2");
        setTimeout(()=>{
          this.launch({skipLogin:true, keepBack:true})
          }, 1000);
      }else{
        this.showPage("3b_2");
      }         
    },

    showFTE(id) {
      $('.fte').hide();
      $('.fte_' + id).show();
    },

    showDownload(id) {
      $('.download_' + id).show();
      if(id == '2_8'){
        $('.fte_2_7').hide();
      }
    },

    start(scenario, option) {
      if(option && option.skipDialogPage){
        this.skipDialogPage = option.skipDialogPage;
      }

      $('#ptcover').remove();
      $('#desktop').show();

      if(scenario == '3a'){
        this.$store.commit('meetingAppShowSplash', false);
        this.$store.commit('meetingAppVisible', true);
        this.startInCallTimer();
        return;
      }

      $('.slideshow').show();
      $('.scenario'+scenario).show();
      this.showPage(scenario+'_1');

      if(scenario == 1){
        this.$store.commit('meetingAppLogedIn', false);
        this.$store.commit('interstitialSkipLogin', false);
      }else if(scenario == 2){
        this.$store.commit('meetingAppLogedIn', true);
        this.$store.commit('interstitialSkipLogin', true);
      }

      setTimeout(() => {
        //this.launchFullscreen();
      }, 50);

      /*
      if(!firstTime){
        this.$store.commit('skipInterstitial', true);
        this.$store.commit('meetingAppVisible', true);
        $('#pre-flow').hide();
      }
      */

    },

    showMdp(){
      $('.scenario').hide();
      $('.scenario_mdp').show();
      this.showPage('mdp');
    },

    gotoPushDownload(){
      $(".scenario1").show();
      $(".scenario2").hide();
      this.showPage("1_2");
      this.change2TestMeeting();
    },

    change2TestMeeting() {
      this.$store.commit('meetingTitle', 'Test Meeting');
    },

    dismissAccessCam() {
      $(".access_cam").hide();
      $(".page_1b_2 .spinner").hide();
      $("#camera_thin_client").show();
    },

    launch(option) {
      if(!option || !option.keepBack){
        $('.slideshow').hide();
      }
      
      $('#taskbar .ie').hide();

      if(option && option.skipLogin){
        this.$store.commit('initSelfCam', true);
      }else{
        this.$store.commit('interstitialFirstTime', true);
      }

      this.$store.commit('showSelectedDevices', true);
      $('#mainclient').show();
      
    },

    launchFullscreen() {
      let element = document.documentElement;
      if (element.requestFullscreen) {
        element.requestFullscreen();
      } else if (element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if (element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    },

    // -----------------------------------
    //          in call timer
    // -----------------------------------
    setInCallTimer(){
    
      function formatNum(n){
        if(n < 10){
          return ('0'+n);
        }else{
          return n;
        }
      }
      
      this.seconds_last++;
      var m = Math.floor(this.seconds_last/60);
      var s = this.seconds_last%60
      
      $('.meetingalert .timer').text(formatNum(m) + ':' + formatNum(s));

    },
    startInCallTimer(){
      if(this.timer_interval_id == 0){
        this.timer_interval_id = setInterval(() => this.setInCallTimer(), 1000);
        this.setInCallTimer();
      }
    },
    stopInCallTimer(roomid){
      clearInterval(this.timer_interval_id);
      this.timer_interval_id = 0;
    },

    
    
  },
};
</script>

