<script>
export default {
  data() {
    return {
      domParent: null,
      domSelf: null,
      cameraSelected: true,
    };
  },
  computed: {
    audioType() {
      return this.$store.state.audioType;
    },
    popAVSettingVisible() {
      return this.$store.state.popAVSettingVisible;
    },
  },
  mounted() {
    $('.select').selectmenu({
      select: (evt, ui) => {
        if (evt.target.id == 'select_cam_conn') {
          if (ui.item.value == 'nocam') {
            this.cameraSelected = false;
            $('#video_preview').hide();
          } else {
            this.cameraSelected = true;
            $('#video_preview').show();
            this.$store.commit('selectedCameraName', ui.item.label);
          }
        }
      },
    });
    this.domParent = $('#con_main');
    this.domSelf = $('#pop_ac_setting');
    window.addEventListener('mousedown', this.onMouseDown);
  },
  watch: {
    
  },
  methods: {
    close(evt) {
      this.$store.commit('popAVSettingVisible', false);
    },
    onMouseDown(evt){
      if($('.ui-selectmenu-open').length){
        if( !$('.ui-selectmenu-open').hitTest(evt.pageX, evt.pageY) && $('#pop_ac_setting').length && !$('#pop_ac_setting').hitTest(evt.pageX, evt.pageY) ){
            this.$store.commit('popAVSettingVisible', false);
        }
      }else{
        if( $('#pop_ac_setting').length && !$('#pop_ac_setting').hitTest(evt.pageX, evt.pageY) ){
            this.$store.commit('popAVSettingVisible', false);
        }
      }
    },
  },
  beforeDestroy(){
      window.removeEventListener('mousedown', this.onMouseDown);
  },
};
</script>

