<template>
    <transition name="fade">
	<div id='pop_audio' v-show='popAudioVisible' >
        <div class='title'>
            {{devicePaired ? 'Switch Audio Connection' : 'Audio Connection'}}
            <div class='close' v-on:click.stop='close' ></div>
        </div>
        <div class='connection_status_section' v-if='!devicePaired'>
            <div class='icon_sts' v-bind:class='audioType'></div>
            <div class='label'>
                {{audioType === "none" ? "You’re not connected to audio" : ""}}
                {{audioType === "pc" ? "You are using computer for audio" : ""}}
                {{audioType === "callme" ? "We’re calling you at +1 408-123-4567" : ""}}
                {{audioType === "callin" ? "You’re calling in" : ""}}
                <div class='settings' v-show='audioType === "pc"'></div>
            </div>
            <div class='btn_disconnect uibutton red s32' v-if='audioType !== "none"' v-on:click='switchAudio("none")'>Disconnect</div>
        </div>
        <div class='title_option' v-if='!devicePaired'>
            {{audioType !== 'none' ? 'Switch Audio' : 'Audio Options'}}
        </div>
        <div class='switch_from_device' v-if='devicePaired'>
            When you switch to one of the following audio connection options, you'll disconnect from your video system.
        </div>
        <div class='wrap'>
            <div class='itm menuitm con_flex_row btn_pc' v-if='audioType !== "pc"' v-bind:class='{selected:selectedAudioType=="pc"}' v-on:mouseover='selectAudioType("pc")' v-on:mouseout='selectAudioType()' >
                <div class='ico flex_none'></div>
                <div class='colr flex_1'>
                    <div class='lb'>Use computer for audio</div>
                    <div class='settings'></div>
                </div>
                <div class='uibutton blue s24' v-if='selectedAudioType=="pc"' v-on:click='switchAudio("pc")'>{{audioType !== 'none' || devicePaired ? 'Switch' : 'Connect'}}</div>
            </div>

            <div class='itm menuitm con_flex_row btn_callme' v-if='audioType !== "callme" && audioType !== "callin"' v-bind:class='{selected:selectedAudioType=="callme"}' v-on:mouseover='selectAudioType("callme")' v-on:mouseout='selectAudioType()' >
                <div class='ico flex_none'></div>
                <div class='colr flex_1'>
                    <div class='lb'>Call me at <span class="num">+1 (408) 123-4567</span></div>
                    <div class='arr'></div>
                    <span></span>
                </div>
                <div class='uibutton blue s24' v-if='selectedAudioType=="callme"' v-on:click='switchAudio("callme")'>{{audioType !== 'none' || devicePaired ? 'Switch' : 'Connect'}}</div>
            </div>

            <div class='itm menuitm con_flex_row btn_callin' v-if='audioType !== "callme" && audioType !== "callin"' v-bind:class='{selected:selectedAudioType=="callin"}' v-on:mouseover='selectAudioType("callin")' v-on:mouseout='selectAudioType()' >
                <div class='ico flex_none'></div>
                <div class='colr flex_1'>
                    <div class='lb'>Call In</div>
                    <span></span>
                </div>
                <div class='uibutton blue s24' v-if='selectedAudioType=="callin"' v-on:click='showCallin()'>View</div>
            </div>

        </div>

        <div class='overlay_disconnecting' v-if='disconnecting'>
            <div class='spinner'></div>
            <div class='lb'>Disconnecting…</div>
        </div>

        <div class='overlay_callin' v-if='callinVisible' >
            <div class='title'>
                Call In
                <div class='close' v-on:click.stop='close' ></div>
            </div>
