</template>

<script>

export default {
    data() {
        return {
            domParent: null,
            domSelf: null,
            selectedAudioType: null,
            disconnecting: false,
            callinVisible: false,
        }
    },
    computed:{
        popAudioVisible(){
            return this.$store.state.popAudioVisible;
        },
        popStartPoint(){
            return this.$store.state.popStartPoint;
        },
        audioType(){
            return this.$store.state.audioType;
        },
        devicePaired(){
            return this.$store.state.devicePaired;
        },
    },
    mounted(){
        this.domParent = $('#con_main');
        this.domSelf = $('#pop_audio');
    },
    watch: {
        popAudioVisible: function (newVal) {
            if(newVal){
                this.selectedAudioType = this.audioType;
                let left = this.popStartPoint[0];
                let top = this.popStartPoint[1] - 20;
                //$('#pop_audio').css('left', left+'px');
                //$('#pop_audio').css('top', top+'px');
                window.addEventListener('mousedown', this.onMouseDown);
            }else{
                window.removeEventListener('mousedown', this.onMouseDown);
            }
        },

    },
    methods:{
        onMouseDown(evt){
            if( ($('#controls .btn_audio').length>0 && !$('#controls .btn_audio').hitTest(evt.pageX, evt.pageY) || $('#controls .btn_mute').length>0 && !$('#controls .btn_mute').hitTest(evt.pageX, evt.pageY)) && this.domSelf.length && !this.domSelf.hitTest(evt.pageX, evt.pageY) ){
                this.close();
            }
        },
        selectAudioType(type){
            this.selectedAudioType = type;
        },
        showCallin() {
            this.callinVisible = true;
        },
        switchAudio(type){
            this.$store.commit('devicePaired', false); 
            this.$store.commit('audioType', type);
            this.$store.commit('audioConnected', type !== 'none');

            if (type === 'none') {
                this.disconnecting = true;
                setTimeout(() => {
                    this.disconnecting = false;
                }, 1500);
            }
        },
        close(evt){
            this.callinVisible = false;
            this.$store.commit('popAudioVisible', false);
        }
    }

}
</script>

