<template>
  <div id="pre-flow">
    <div class='taskbaricon ie'></div>
    <div class='page page1'>
      <div class="btn btn_next" v-on:click='showPage(2)'></div>
    </div>
    <div class='page page2'>
      <div class="btn btn_next" v-on:click='showPage(3)'></div>
    </div>
    <div class='page page3'>
      <div class='loading'>
        <div class='bar'></div>
      </div>
      <div class='percent'>{{percent + '%'}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      percent: 0
    };
  },
  computed: {
    
  },
  mounted() {
    this.showPage(1);
  },
  methods: {
    showPage(id) {
      $('.page').hide();
      $('.page' + id).show();

      if(id === 3){
        $('.loading .bar').css('width', 0);
        $('.loading .bar').velocity(
          { width: '100%' },
          {
            duration: 2000,
            queue: false,
            progress: (elements, complete, remaining, start, tweenValue) => {
              this.percent = Math.round(complete * 100);
              if(complete === 1){
                this.launch();
              }
            },
          }
        );
      }
    },
    launch() {
      this.showPage(4);
      $('.taskbaricon.ie').hide();
      this.$store.commit('interstitialFirstTime', true);
      this.$store.commit('showSelectedDevices', true);
      $('#mainclient').show();
    },
  },
};
</script>

