<template>
    <transition name="fade">
  <div id='pop_video' v-show='popVideoVisible' >
        <div class='triangle'></div>
        <div class='close' v-on:click.stop='close' ></div>
        <div class='wrap'>
            <div class='title'>{{popVideoIsSwitch ? 'Switch Camera' : 'Video Connection'}}</div>

            <select id='cam_list'>
              <option value='cam1' selected='selected'>Microsoft LifeCam Font</option>
              <option value='cam2' >Laptop Camera</option>
              <option value='nocam' >No Video</option>
            </select>

            <div class='video' id="camera_preview_area">
                <video id="camera_preview" autoplay="true"></video>
                <div class='lb'>{{popVideoIsSwitch ? 'Sending' : 'Preview Only'}}</div>
                <div v-if='popVideoIsSwitch && showCameraLabel' class='lb_selected'>{{selectedCameraName}}</div>
            </div>

            <div class='btn_start uibutton green' v-if='!popVideoIsSwitch && !cameraOn' v-on:click.stop='sendVideo'>Start My Video</div>
            <div class='btn_stop uibutton red' v-if='!popVideoIsSwitch && cameraOn' v-on:click.stop='stopVideo'>Stop My Video</div>

        </div>
        
    </div>
    </transition>
</template>

<script>
export default {
  data() {
    return {
      domParent: null,
      domSelf: null,
      showCameraLabel: false,
    };
  },
  computed: {
    cameraOn() {
      return this.$store.state.cameraOn;
    },
    popVideoVisible() {
      return this.$store.state.popVideoVisible;
    },
    popVideoIsSwitch() {
      return this.$store.state.popVideoIsSwitch;
    },
    popStartPoint() {
      return this.$store.state.popStartPoint;
    },
    videoConnected() {
      return this.$store.state.videoConnected;
    },
    selectedCameraName() {
      return this.$store.state.selectedCameraName;
    },
  },
  mounted() {
    this.domParent = $('#con_main');
    this.domSelf = $('#pop_video');
    this.initCam();
    this.showCameraLabel = false;

    $('#cam_list').selectmenu({
      select: (evt, ui) => {
        this.$store.commit('selectedCameraName', ui.item.label);
        if (ui.item.value == 'nocam') {
          this.cameraSelected = false;
          $('#camera_preview_area').css('opacity', 0);
          $('#camera_preview').hide();
        } else {
          this.showCameraLabel = true;
          this.cameraSelected = true;
          $('#camera_preview_area').css('opacity', 1);
          $('#camera_preview').show();
        }
      },
      open: (evt) => {
        if (this.popVideoIsSwitch) {
          $('#cam_list-menu .ui-menu-item:last').hide();
        } else {
          $('#cam_list-menu .ui-menu-item:last').show();
        }
      },
    });
    
  },
  update() {},
  watch: {
    popVideoVisible: function(newVal) {
      if (newVal) {
        let left = this.popStartPoint[0];
        let top = this.popStartPoint[1] - 20;
        $('#pop_video').css('left', left + 'px');
        $('#pop_video').css('top', top + 'px');
        window.addEventListener('mousedown', this.onMouseDown);
      } else {
        window.removeEventListener('mousedown', this.onMouseDown);
      }
    },
  },
  methods: {
    initCam() {
      var selfViewVideo = document.getElementById('camera_preview');

      // Prefer camera resolution nearest to 1280x720.
      var constraints = { audio: false, video: { width: 1280, height: 720 } };
      navigator.mediaDevices
        .getUserMedia(constraints)
        .then(function(mediaStream) {
          var video = selfViewVideo;
          video.srcObject = mediaStream;
          video.onloadedmetadata = function(e) {
            video.play();
          };
        })
        .catch(function(err) {
          console.log(err.name + ': ' + err.message);
        });
    },
    onMouseDown(evt) {
      let btn;
      if ($('#controls .btn_cam').length > 0) {
        btn = $('#controls .btn_cam');
      } else {
        btn = $('#controls .btn_video');
      }
      if (
        !btn.hitTest(evt.pageX, evt.pageY) &&
        this.domSelf.length &&
        !this.domSelf.hitTest(evt.pageX, evt.pageY)
      ) {
        this.close();
      }
    },
    sendVideo(evt) {
      this.$store.commit('videoConnected', true);
      this.$store.commit('cameraOn', true);
      this.close();
    },
    stopVideo(evt) {
      this.$store.commit('cameraOn', false);
      this.close();
    },
    close(evt) {
      this.showCameraLabel = false;
      this.$store.commit('popVideoVisible', false);
    },
  },
};
</script>

<style>
#cam_list-button {
  margin-top: 12px;
  width: 100%;
}
</style>

